<template>
  <div class="bridge_box">
    <div class="exchange_box">
      <div class="exchange_top">
        <p>Bridge</p>
      </div>
      <div class="exchange_bottom">
        <a-row align="middle" justify="center" :gutter="[0,12]">
          <a-col :span="24" :order="fromAndTo?3:1">
            <div class="exchange_item_box">
              <div class="exchange_item_top flex_row_space_between">
                <h5>From<span>{{fromToken.chainName}}</span></h5>
                <p>{{'Balance: ' + fromToken.balance}}</p>
              </div>
              <a-input
                  class="exchange_item_input"
                  size="large"
                  v-model:value="fromValue"
                  placeholder="0.0"
                  :bordered="false"
                  :max-length="50"
                  :onkeyup="fromValue=fromValue.match(/\d+(\.\d{0,8})?/) ? fromValue.match(/\d+(\.\d{0,8})?/)[0] : ''"
                  @change="onChange"
              >
                <template #suffix>
                  <a-button type="text" @click="onFromMax">Max</a-button>
                  <a-dropdown :trigger="['click']">
                    <div class="flex_row" style="cursor: pointer">
                      <img :src="fromToken.icon" alt=""/>
                      <h4>{{fromToken.name}}</h4>
                      <svg width="12" height="7" viewBox="0 0 12 7" fill="none" class="sc-dyGzUR fjZPDO"><path d="M0.97168 1L6.20532 6L11.439 1" stroke="#000000"></path></svg>
                    </div>
                    <template #overlay>
                      <a-menu>
                        <a-menu-item v-for="item in fromData" @click="onFromCheck(item)">
                          <div class="flex_row">
                            <img style="width: 20px;height: 20px; border-radius: 50%;" :src="item.icon" alt=""/>
                            <h4 style="font-size: 14px;color: rgb(0, 0, 0);padding-left: 5px">{{item.name}}({{item.chainName}})</h4>
                          </div>
                        </a-menu-item>
                      </a-menu>
                    </template>
                  </a-dropdown>
                </template>
              </a-input>
            </div>
          </a-col>
          <a-col :span="24" :order="2" class="flex_column">
            <a-button type="text" shape="circle" @click="onSwitch">
              <img style="" src="../assets/img/arrow-down-grey.svg" alt=""/>
            </a-button>
          </a-col>
          <a-col :span="24" :order="fromAndTo?1:3">
            <div class="exchange_item_box">
              <div class="exchange_item_top flex_row_space_between">
                <h5>To<span>{{toToken.chainName}}</span></h5>
                <p>{{'Balance: ' + toToken.balance}}</p>
              </div>
              <a-input
                  class="exchange_item_input"
                  size="large"
                  v-model:value="toValue"
                  placeholder="0.0"
                  :bordered="false"
                  disabled
              >
                <template #suffix>
                  <a-dropdown :trigger="['click']">
                    <div class="flex_row" style="cursor: pointer">
                      <img :src="toToken.icon" alt=""/>
                      <h4>{{toToken.name}}</h4>
                      <svg width="12" height="7" viewBox="0 0 12 7" fill="none" class="sc-dyGzUR fjZPDO"><path d="M0.97168 1L6.20532 6L11.439 1" stroke="#000000"></path></svg>
                    </div>
                    <template #overlay>
                      <a-menu>
                        <a-menu-item v-for="item in toData" @click="onToCheck(item)">
                          <div class="flex_row">
                            <img style="width: 20px;height: 20px; border-radius: 50%;" :src="item.icon" alt=""/>
                            <h4 style="font-size: 14px;color: rgb(0, 0, 0);padding-left: 5px">{{item.name}}({{item.chainName}})</h4>
                          </div>
                        </a-menu-item>
                      </a-menu>
                    </template>
                  </a-dropdown>
                </template>
              </a-input>
            </div>
          </a-col>
        </a-row>
        <a-button
            block
            class="exchange_btn"
            @click="onSwap"
            :loading="swapLoad"
            :disabled="(fromValue=='' || toValue=='')"
            v-if="(address && address.length > 0)"
            type="primary">
          {{ (fromValue!='' && toValue!='')?btnTitle:'Enter an amount' }}
        </a-button>

        <a-button
            block
            class="exchange_btn"
            @click="login"
            type="default"
            v-else
        >
          {{ 'Connect Wallet' }}
        </a-button>
      </div>
    </div>
  </div>
</template>

<script>
import MathUtil from '@/utils/MathUtil';
import { defineComponent, reactive, toRefs, onMounted, computed, watch } from 'vue';
import { useStore } from 'vuex';
import Web3Util from '@/utils/Web3Util';

export default defineComponent({
  name: "Bridge",
  setup() {
    const store = useStore();
    const Data = reactive({
      address: computed(() => { return store.state.wallet_address }),
      swapLoad: false,
      fromValue: '',
      toValue: '',
      btnTitle:'Swap',
      fromToken:{icon:require('@/assets/img/bnb-usdt.png'),name:'USDT',chainId:'56',chainName:'BNB',balance:'~'},
      toToken:{icon:require('@/assets/img/oort-usdt.png'),name:'USDT',chainId:'970',chainName:'OORT',balance:'~'},
      fromData: [
        {icon:require('@/assets/img/bnb-usdt.png'),name:'USDT',chainId:'56',chainName:'BNB',balance:'~'},
        {icon:require('@/assets/img/oort-usdt.png'),name:'USDT',chainId:'970',chainName:'OORT',balance:'~'},
        // {icon:require('@/assets/img/ccn-usdt.png'),name:'USDT',chainId:'828',chainName:'CCN',balance:'~'}
      ],
      toData: [
        {icon:require('@/assets/img/oort-usdt.png'),name:'USDT',chainId:'970',chainName:'OORT',balance:'~'},
        // {icon:require('@/assets/img/ccn-usdt.png'),name:'USDT',chainId:'828',chainName:'CCN',balance:'~'}
      ]
    })

    watch(() => [store.state.wallet_address], () => Methods.refresh())

    onMounted(() => { Methods.refresh() })

    const Methods = {
      async refreshFromBalance(){
        if(Data.fromToken && store.state.wallet_address){
            const chainId = Data.fromToken.chainId;
            const contract = Web3Util.getUsdtContract(chainId)
            let bls = await contract.methods.balanceOf(store.state.wallet_address).call();
            Data.fromToken.balance = Web3Util.getUsdtBalance(chainId,bls);
          }
      },
      async refreshToBalance(){
        if(Data.toToken && store.state.wallet_address){
            const chainId = Data.toToken.chainId;
            const contract = Web3Util.getUsdtContract(Data.toToken.chainId)
            let bls = await contract.methods.balanceOf(store.state.wallet_address).call();
            Data.toToken.balance = Web3Util.getUsdtBalance(chainId,bls);;
          }
      },
      async refresh() {
        if(store.state.wallet_address){
          Methods.refreshFromBalance()
          Methods.refreshToBalance()
        }
      },
      async onFromMax() {
        Data.fromValue = Data.fromToken.balance
        Methods.onChange();
      },
      async onFromCheck(item) {
        Data.fromToken = item;
        store.dispatch('switchChainid',Data.fromToken.chainId);
        const froms = Data.fromData;
        const tos = Array()
        if(Data.fromToken.chainId=="56"){
          for(var i=0;i<froms.length;i++){
            let item = froms[i];
            if(item.chainId!=Data.fromToken.chainId){
              tos.push({...item})
            }
          }
        }else{
          tos.push({...Data.fromData[0]})
        }
        Data.toData = tos;
        if(Data.fromToken.chainId==Data.toToken.chainId){
          Data.toToken = Data.toData[0];
        }
        Methods.refreshFromBalance()
        Methods.refreshToBalance()
        //switch wallet
        if(store.state.chainId!=Data.fromToken.chainId){
          store.dispatch('showWalletModal',true);
          return;
        }
      },
      async onToCheck(item) {
        Data.toToken = item;
        Methods.refreshToBalance()
      },
      async login() {
        store.dispatch('showWalletModal',true);
      },
      async onChange(){
        Data.toValue = MathUtil.times(Data.fromValue,"0.99",4) 
      },
      async onSwap(){
        try{
          if(store.state.chainId!=Data.fromToken.chainId){
            store.dispatch('showWalletModal',true);
            return;
          }
          Data.swapLoad = true;
          const chainId = Data.fromToken.chainId;
          const from = store.state.wallet_address;
          const brigeAddr = Web3Util.getBridgeContractAddress(chainId);
          const contract = Web3Util.getUsdtContract(chainId)
          const gasPrice = Web3Util.getGass(chainId)
          const balance = await contract.methods.balanceOf(from).call();
          const payAmount = MathUtil.timesPow(Data.fromValue,18,0);
          const min = await Web3Util.getBridgeContractW(chainId).methods.minAmount().call();
          if(!MathUtil.gte(payAmount,min)){
            Data.swapLoad = false;
            window.notification["error"]({message:"Error",description:'Minimum amount '+MathUtil.divPow(min,18,4)});
            return;
          }
          alert(payAmount+">"+balance)
          if(MathUtil.gt(payAmount,balance)){
            Data.swapLoad = false;
            window.notification["error"]({message:"Error",description:'Insufficient balance'});
            return;
          }
          const amount = await contract.methods.allowance(from,brigeAddr).call();
          if(MathUtil.gt(payAmount,amount)){
            Data.btnTitle = "Approve"
            await Web3Util.getUsdtContractW(chainId).methods.approve(brigeAddr,payAmount).send({from,gasPrice});
          } 
          Data.btnTitle = "Swap"
          await Web3Util.getBridgeContractW(chainId).methods.callBridge(Data.toToken.chainId,payAmount).send({from,gasPrice});
          Data.swapLoad = false;
          window.notification["success"]({message:"Success",description:''});
          //refresh
          Methods.refreshFromBalance()
          Methods.refreshToBalance()
        }catch(e){
          Data.btnTitle = "Swap"
          Data.swapLoad = false;
          window.notification["error"]({message:"Error",description:JSON.stringify(e)});
        }
      }
    }
    return { ...toRefs(Data), ...Methods }
  }
})
</script>

